@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

body.login {
  background-color: #FFFFFF;
}

body.login div#login p#nav {
  display: none;
}

body.login div#login form#loginform p.submit input#wp-submit {
  background-color: #084C61 !important;
  color: #FFFFFF;
  border: 1px solid #084C61;
  margin: 10px 10px 10px 10px;
}

body.login div#login h1 a {
  background-image: url("../../img/sl-login.png");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 172px;
  height: 70px;
  width: 172px;
}

body.login div#login form#loginform input {
  font-size: 15px;
  color: #084C61;
}

.login form {
  background-color: #E8EDEF;
  border-radius: 2px;
  font-weight: 400;
  margin-left: 0;
  margin-top: 20px;
  overflow: hidden;
  padding: 1.5rem 1rem;
}

.login form .input, .login form input[type=checkbox], .login input[type=text] {
  background: #FFFFFF;
}

.wp-core-ui .button-primary {
  box-shadow: unset !important;
  text-shadow: unset !important;
}
